import { forwardRef } from "react";
import {
  Contact,
  FooterContainer,
  FooterWrapper,
  Maps,
  QuestionsContainer,
  Info,
  QuestionsTitle,
  FooterText,
} from "./styled";
import { GoogleMap, LoadScript, Marker } from "@react-google-maps/api";

const FooterComponent = forwardRef((props, ref) => {
  const defaultCenter = { lat: 51.267180, lng: 22.562455 }; 

  return (
    <FooterContainer>
      <FooterWrapper>
        <Contact ref={ref}>Kontakt</Contact>
        <QuestionsContainer>
          <QuestionsTitle>Masz pytania?</QuestionsTitle>
          <Info>Safeko house sp. z o.o.</Info>
          <Info>ul. Mackiewicza 21/31, 20-865 Lublin</Info>
          <Info isMiddle>kontakt@safekohouse.com</Info>
          <Info>+48 609 505 111</Info>
          <Info isLast>NIP: 712 338 48 46</Info>
          <Info>KRS: 0000779078</Info>
        </QuestionsContainer>
        <Maps>
          <LoadScript
            googleMapsApiKey={process.env.REACT_APP_GOOGLE_MAPS_API_KEY}
          >
            <GoogleMap
              mapContainerStyle={{ width: "100%", height: "400px" }} 
              center={defaultCenter}
              zoom={13}
            >
              <Marker position={defaultCenter} />
            </GoogleMap>
          </LoadScript>
        </Maps>
        <FooterText> Safeko house</FooterText>
        <FooterText>Polityka prywatnoci</FooterText>
        <FooterText isLast>
          Stosujemy pliki cookies. Korzystanie ze strony bez zmiany ustawie
          przegldarki oznacza, e pliki cookies bd zamieszczane w Twoim
          urzdzeniu. Czytaj wicej
        </FooterText>
      </FooterWrapper>
    </FooterContainer>
  );
});

export default FooterComponent;
