 import { forwardRef } from "react";
import {
  DescFirst,
  ItemFirst,
  Offer,
  OfferContainer, 
  StyledSection,
  TitleFirst,
} from "./styled";

const OfferSection = forwardRef((props, ref) => { 

  return (
    <StyledSection>
      <OfferContainer>
        <Offer ref={ref}>Oferta</Offer>
        <ItemFirst>
          <TitleFirst>Rezydencja Poligonowa</TitleFirst>
          <DescFirst>
            Domy projektujemy z myl o sobie i naszych rodzinach. To inwestycje
            nowoczesne i funkcjonalne, a przede wszystkim przyjazne mieszkacom.
            Nasze lokale ceni wszyscy, ktrzy nie chc y w wielkomiejskim
            toku.
          </DescFirst>
        </ItemFirst> 
      </OfferContainer> 
    </StyledSection> 
  );
});

export default OfferSection;
